@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap');

$font-1: 'Roboto Condensed', sans-serif;

$color-1: #609db1;
$color-1: #FFB938;
$color-2: #6C77F4;
// $color-3: rgb(99, 164, 233);
$color-3: #FFA500;
$color-4: #4FEDD2;
// $color-5: #76b9ff;
$color-5: #ee5a04;
$color-6: #1f1e1e;
// $color-7: rgb(212, 212, 212);   
$color-7: #ff5e00d8;   
// $color-8: #ffffff;
$color-8: #ff7b00;
$white: #ddd;
$black: #333;

$borderRadius: 25px;
$shadow: 0 2px 3px #333;
$minHeight: 98vh;

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center; 
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  margin: 5px ;
  max-width: 100%;
  font-family: $font-1;
   color: white;
   background-color: #ffffff;
}
button {
  cursor: pointer;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: $black;
  cursor: pointer;
}
a:visited {
  color:$black;
}
.home, .knowledges, .portfolio, .contact, .legalNotice {
  display: flex;

  @media screen and (max-width: 850px){
    display: block !important;
  }
}

.homeContent, .knowledgesContent, .portfolioContent, .contactContent, .legalNoticeContent  {
  margin-left: 5px;

  @media screen and (max-width: 850px){
    margin-left: 0 !important;
  }
}